 export default {
      data() {
        return {
          ruleForm: {
            IDNum: '',
            Tel: '',
            Telcode:'',
            Pwd: '',
          },
          loading:false,
          rules:{
				IDNum:[
						{
						required:true,
		      			message:'身份证号不能为空',
		      			trigger:'blur'
						}
					],
				Tel:[
						{
						required:true,
		      			message:'联系电话不能为空',
		      			trigger:'blur'
						}
					],
				Telcode:[
					{
					required:true,
	      			message:'短信验证码不能为空',
	      			trigger:'blur'
					}
				],
				Pwd:[
						{
						required:true,
		      			message:'密码不能为空',
		      			trigger:'blur'
						}
					],
			}
        }
      },
      methods: {
			submitForm() {
				this.$refs.ruleForm.validate((valid)=>{
					if(valid){
						 this.$api.put('gyswallet/createGysWalletInfo',{
						 	'idCard':this.ruleForm.IDNum,
						 	'tel':this.ruleForm.Tel,
						 	'telcode':this.ruleForm.Telcode,
						 	'password':this.ruleForm.Pwd
						 	
						 },localStorage.token, success => {
				 			this.$router.push({ path: '/Nav1/Zhizhibao.js' });
						  },
						 failure=>{
						   this.$message({
					            type: 'error',
					            message: failure.msg
					       });
						 })
						 
					}
				})
			},
			getTelCode(){
				if(this.ruleForm.Tel!=""){
					 this.$api.post('gyswallet/createGysWalletSendEmail/'+this.ruleForm.Tel,{},localStorage.token, success => {
				 			this.$message({
					            type: 'success',
					            message: success.msg
					          });
						  },
						 failure=>{
						   this.$message({
					            type: 'error',
					            message: '获取短信验证码失败'
					      	});
						 })
				}else{
					this.$message({
		            type: 'waring',
		            message: '手机号码不能为空'
		          });
				}
				
			}
			}

    }


